

import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: '',
components: {
Group,
XInput,
XButton,
Toast
},
data () {
return {
showToast: false,
toastText: '',
showToast1: false,
toastText1: '',
buttonStatus: false,
counts: 60,
json: {
refpassword: '',
password: '',
password1: ''
}
}
},
destroyed () { // 销毁时显示
this.$store.commit('components', [true, true, true])
},
mounted () { // 隐藏
this.$store.commit('components', [true, false, true])
},
methods: {
submit () {
console.log('s')
let text = ''
let status = 0
if (!this.json.refpassword.trim()) {
text = '请填写旧密码'
} else if (!this.json.password.trim()) {
text = '请填写新密码'
} else if (!this.json.password1.trim()) {
text = '请确认新密码'
} else if (this.json.password.trim() !== this.json.password1.trim()) {
text = '两次密码不一致'
} else {
status = 1
}
if (status) { // 修改成功时
this.reset()
} else {
console.log('fail')
this.toast1(text)
}
},
async reset () {
let result = await this.axios.post('member/memberpwdedit', this.json)

if (result.success) {
this.toast('已成功修改')
let timer = setTimeout(() => {
this.$router.push({name: 'my'})
clearTimeout(timer)
}, 1000)
} else {
this.toast1(result.message)
return false
}
},
toast (mes) { // 成功弹窗
this.showToast = true
this.toastText = mes
},
toast1 (mes) { // 失败弹窗
this.showToast1 = true
this.toastText1 = mes
}
}
}



